import {React, useEffect} from 'react'
import './Info.css'
import image1 from '../images/print4.jpg'

export default function Information5(props){

    useEffect(()=>{

        window.addEventListener('scroll', scrollAnimate);
        
        function scrollAnimate(){

            let container = document.getElementById('info4-container');
            let rect = container.getBoundingClientRect();
  
            if (window.pageYOffset >= 4*rect.top && window.pageYOffset <= rect.top + 5*container.clientHeight){
                container.classList.remove('info4-container-start');
                container.classList.add('info4-container');
            } else {
                container.classList.remove('info4-container');
                container.classList.add('info4-container-start');
            }
          }
        
        return ()=>window.removeEventListener('scroll',scrollAnimate);
    })


   return(

    <section id = "info5-container" className="info5-container">
        <div className="info5-wrap">
        <h1 className="heading5">{props.heading}</h1>
        <div className="info5-wrapper">
        <div className = "image-box">
        <img className = "info-image" src={image1} alt = "image5"></img>
        </div>
        <div id = "info5-1" className ="info5-1">
        <p className="text">I hope this was a good way of presenting myself.</p>
        <p className="text">Best Regards,</p>
        <p className="text">Bernardo Almeida</p>
        </div>
        </div>
        </div>
    </section>

   )
 }

