import {React, useState, useEffect} from 'react';
import {Link} from 'react-scroll';
import './Scroll.css'

import scroll from './icons/scroll.svg';

export default function Scroll(){

    const [counter,setCounter] = useState(0)
    const [transition, setTransition] = useState(false);

    useEffect(()=>{

        let interval = setInterval(()=>setCounter(counter+1),500);
        let scroll = document.getElementById("scroll");

        let container = document.getElementById('info5-container').getBoundingClientRect();
        if(window.pageYOffset >= container.bottom){
            scroll.classList.remove('scroll-container');
            scroll.classList.add('scroll-container-end');
        } else {
            scroll.classList.add('scroll-container');
            scroll.classList.remove('scroll-container-end');
        }

        //toggle SetSum
        if(counter % 2 === 0){
            setTransition(false);
        } else{
            setTransition(true);
        }

        if(transition){
            scroll.classList.add("scroll-animation");
        } else {
            scroll.classList.remove("scroll-animation");
        }

        scroll.style.transition = "top 0.5s ease-in-out";

        return () => clearInterval(interval); 

    },[counter,transition,setTransition])

    return (
        <div className="scroll-container">
            <div className="sc-head1"><h1>FARFETCH Plugin 2021</h1></div>
           
            <div className="sc-arrow">
            <img id = "scroll" className="scroll-arrow" src={scroll} alt = 'scroll'></img>
            </div>
        </div>
    )
}